@import './../../styles/utils/vars';
@import './../../styles/utils/mixins';
@import './../../styles/utils/activeLink';
@import './../../styles/utils/icons';
@import './../../styles/utils/container';

.footer {
  box-shadow: 0 -1px 0 0 $shadow-color;
  bottom: 0;

  &__logo {
    margin: 0;
    height: 32px;
    width: 89px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding-block: 32px;
    box-sizing: border-box;
  }

  &__centerBlock {
    display: flex;
    flex-direction: column;
    gap: 16px;

    &--link {
      padding: 0;
      width: min-content;
    }
  }

  &__rightBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;

    &--link {
      text-transform: initial;
      font-family: MontSemiBold, sans-serif;
      padding: 0;
      cursor: pointer;
    }

    &--imgLink {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid $arrow-border-color;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      cursor: pointer;
    }

    &--img {
      width: 16px;
      height: 16px;
      background-image: url(./../../images/Footer/Vector.svg);
    }
  }

  @include on-tablet {
    &__container {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding-inline: 32px;
    }

    &__centerBlock {
      flex-direction: row;
      gap: 13.5px;
    }
  }

  @include on-desktop {
    &__centerBlock {
      gap: 107px;
    }
  }
}
