@import './../../../../styles/utils/vars';
@import './../../../../styles/utils/mixins';


.category {
  &__block {
    @include page-grid;

    row-gap: 32px;
    margin-top: 24px;

    &--title {
      text-decoration: none;
    }

    &--photo {
      display: block;
      width: 100%;
      border-radius: 5%;
      margin-bottom: 24px;
    }

    &--phones,
    &--tablets,
    &--accessories {
      grid-column: 1 / -1;
    }

    @include on-tablet {
      &--phones {
        grid-column: 1 / 5;
      }

      &--tablets {
        grid-column: 5 / 9;
      }

      &--accessories {
        grid-column: 9 / 13;
      }
    }

    @include on-desktop {
      &--phones {
        grid-column: 1 / 9;
      }

      &--tablets {
        grid-column: 9 / 17;
      }

      &--accessories {
        grid-column: 17 / 25;
      }
    }
  }

  &__itemsCounter {
    font-family: MontRegular, sans-serif;
    font-size: 14px;
    font-weight: 600;
    color: $nav-link-color;

    margin-top: 4px;
  }
}
