@import './../../styles/utils/vars';
@import './../../styles/utils/mixins';
@import './../../styles/utils/activeLink';
@import './../../styles/utils/icons';

.nav {
  height: $small-nav-height;
  width: 100%;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  box-shadow: 0 1px 0 0 $shadow-color;
  position: fixed;
  z-index: 3;
  background-color: $bg-color;

  @include on-desktop {
    height: $big-nav-height;
  }

  &__rightBlock {
    display: flex;
    justify-content: space-between;

    @include on-tablet {
      width: 100%;
    }

    &--left {
      display: none;
      gap: 32px;
      height: 48px;
      margin-left: 16px;

      @include on-tablet {
        display: flex;
      }

      @include on-desktop {
        margin-left: 24px;
        gap: 64px;
        height: 64px;
      }
    }

    &--right {
      display: flex;
      align-items: center;
    }
  }

  &__leftBlock {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include on-tablet {
      width: auto;
    }
  }
}
