@import "./../../styles/utils/vars";
@import "./../../styles/utils/mixins";
@import "./../../styles/utils/button";

.productCard {
  &__item {
    background-color: $white-bg-color;
    display: flex;
    justify-content: center;
    flex-direction: column;

    padding: 32px 31.2px;
    border: 1px solid $shadow-color;
    border-radius: 8px;
    transition-duration: 1s;

    width: 220px;

    @include on-tablet {
      width: auto;
    }
  }

  &__text {
    height: 40px;
  }

  &__imgBlock {
    display: flex;
    justify-content: center;
  }

  &__img {
    object-fit: contain;
    width: 148px;
    height: 139px;

    @include on-tablet {
      width: 173px;
      height: 181px;
    }

    @include on-desktop {
      width: 208px;
      height: 196px;
    }
  }

  &__item-top {
    display: flex;
    flex-direction: column;
    gap: 8px;

    padding-block: 24px 8px;
  }

  &__item-center {
    display: flex;
    gap: 8px;
    flex-direction: column;
    border-top: 1px solid $shadow-color;
    padding-block: 16px;
  }

  &__item-center-param {
    display: flex;
    justify-content: space-between;

    &--left {
      color: $nav-link-color;
    }

    &--right {
      color: $text-color;
      font-family: MontSemiBold, sans-serif;
      font-size: 12px;
    }
  }

  &__item-bottom {
    display: flex;
    justify-content: space-between;

    &--buttonCart {
      width: 100px;
      height: 40px;
      border-radius: 48px;
      background-color: $button-item-color;
      border: none;

      font-family: MontRegular, sans-serif;
      font-size: 14px;
      color: $white-bg-color;
      cursor: pointer;

      @include on-tablet {
        width: 125px;
      }

      @include on-desktop {
        width: 160px;
      }
    }

    &--buttonHeart {
      width: 40px;
      height: 40px;
    }
  }

  &__priceBlock {
    display: flex;
    gap: 8px;

    &--fullPrice {
      color: $nav-link-color;
      text-decoration: line-through;
      font-family: MontRegular, sans-serif;
    }
  }
}
