@import './../../styles/utils/mixins';
@import './../../styles/utils/vars';

.navigationPath {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  gap: 8px;

  @include on-tablet {
    margin-bottom: 40px;
  }

  &__home {
    display: flex;
  }

  &__pathes {
    font-family: MontRegular;
    font-size: 12px;
    font-weight: 700;
    color: $nav-link-color;
    text-decoration: none;

    &--active {
      pointer-events: none;
    }
  }
}
