.icon {
  background-repeat: no-repeat;
  background-position: center;

  &__logo {
    height: 22px;
    width: 64px;
    margin: 13px 16px;
    background-size: contain;
    background-image: url(./../../images/Header/Logo.svg);
  }

  &__menu {
    width: 16px;
    height: 16px;
    padding: 16px;
    background-image: url(./../../images/Header/Menu.svg);
    box-shadow: -1px 0 0 0 $shadow-color;
  }

  @include on-tablet {
    &__menu {
      display: none;
    }

    &__favourites,
    &__cart {
      height: 16px;
      width: 16px;
      padding: 16px;
      box-shadow: -1px 0 0 0 $shadow-color;
    }

    &__favourites {
      background-image: url(./../../images/Header/Favourites.svg);
    }

    &__cart {
      background-image: url(./../../images/Header/ShoppingBag.svg);
    }
  }

  @include on-desktop {
    &__favourites,
    &__cart {
      padding: 24px;
    }

    &__logo {
      width: 80px;
      height: 28px;
      margin: 18px 24px;
    }
  }
}
